<template>
    <form @submit.prevent="submit">
        <div class="wrap">
            <h2>Основная информация</h2>
            <!-- Фамилия* -->
            <div class="form-group" :class="{ 'form-group--error': $v.surname.$error }">
                <label class="form__label">Фамилия</label>
                <input class="form__input" v-model.trim="$v.surname.$model"/>
            </div>
            <div class="error" v-if="!$v.surname.required">Поле с фамилией обязательно для заполнения!</div>
            <div class="error" v-if="!$v.surname.minLength">Фамилия не может быть короче {{$v.surname.$params.minLength.min}} символов.</div>
            <!-- Имя* -->
            <div class="form-group" :class="{ 'form-group--error': $v.name.$error }">
                <label class="form__label">Имя</label>
                <input class="form__input" v-model.trim="$v.name.$model"/>
            </div>
            <div class="error" v-if="!$v.name.required">Поле с именем обязательно для заполнения!</div>
            <div class="error" v-if="!$v.name.minLength">Имя не может быть короче {{$v.name.$params.minLength.min}} символов.</div>
            <!-- Отчество -->
            <div class="form-group" >
                <label class="form__label">Отчество</label>
                <input class="form__input" v-model.trim="$v.patronymic.$model"/>
            </div>
            <!-- Дата рождения -->
            <div class="form-group" :class="{ 'form-group--error': $v.birthday.$error }">
                <label class="form__label">Дата рождения</label>
                <input class="form__input" v-model.trim="$v.birthday.$model"/>
            </div>
            <div class="error" v-if="!$v.birthday.required | !$v.birthday.isBirthday">Поле должно быть введено в формате дд.мм.гггг</div>
            <!-- Номер телефона -->
            <div class="form-group" :class="{ 'form-group--error': $v.phone.$error }">
                <label class="form__label">Номер телефона</label>
                <input class="form__input" v-model.trim="$v.phone.$model"/>
            </div>
            <div class="error" v-if="!$v.phone.phoneValid">Номер телефона должен содержать 11 цифр и начинаться на "7"</div>
            <!-- пол -->
            <div class="form-group">
                <label class="form__label">Пол</label>
                <div id="options">
                    <div class="form-group" v-for="gender in genders" :key="gender.value">
                        <label class="option__label">{{ gender.text }}</label>
                        <input type="radio" v-bind:value="gender.value" v-model="$v.gender.$model">
                    </div>
                </div>
            </div>
            <!-- группа клиентов* -->
            <div class="form-group" :class="{ 'form-group--error': $v.phone.$error }">
                <label class="form__label">Группа клиентов</label>
                <div id="options">
                    <div class="form-group" v-for="option in options" :key="option.value">
                        <label class="option__label">{{ option.text }}</label>
                        <input type="checkbox" v-bind:value="option.value" v-model="$v.multiSelect.$model">
                    </div>
                </div>
            </div>
            <div class="error" v-if="!$v.multiSelect.required">Нужно выбрать один или несколько вариантов!</div>
            <!-- лечащий врач -->
            <div class="form-group">
                <label class="form__label">Лечащий врач</label>
                <select v-model="$v.doctor.$model">
                    <option value="">Не важно</option>
                    <option class="form-group" 
                        v-for="doctor in doctors" 
                        :key="doctor.value" 
                        :value="doctor.value">
                            {{ doctor.text }}
                    </option>
                </select>
            </div>
            <!-- Не отправлять смс(чекбокс) -->
            <div class="form-group">
                <label class="form__label">Не отправлять СМС</label>
                <input type="checkbox" class="form__input" v-model="$v.smsCheck.$model"/>
            </div>
        </div>
        <div class="wrap">
            <h2>Адрес</h2>
             <div class="form-group">
                 <label class="form__label">Индекс</label>
                 <input class="form__input" v-model.trim="$v.index.$model"/>
             </div>
             <div class="form-group">
                 <label class="form__label">Страна</label>
                 <input class="form__input" v-model.trim="$v.country.$model"/>
             </div>
             <div class="form-group">
                 <label class="form__label">Область</label>
                 <input class="form__input" v-model.trim="$v.region.$model"/>
             </div>
             <div class="form-group" :class="{ 'form-group--error': $v.city.$error }">
                 <label class="form__label">Город</label>
                 <input class="form__input" v-model.trim="$v.city.$model"/>
             </div>
             <div class="error" v-if="!$v.city.required">Это обязательное поле!</div>
             <div class="form-group">
                 <label class="form__label">Улица</label>
                 <input class="form__input" v-model.trim="$v.street.$model"/>
             </div>
             <div class="form-group">
                 <label class="form__label">Дом</label>
                 <input class="form__input" v-model.trim="$v.house.$model"/>
             </div>
            <h2>Паспорт</h2>
             <div class="form-group" :class="{ 'form-group--error': $v.documentType.$error }">
                <label class="form__label">Тип документа</label>
                <select v-model="$v.documentType.$model">
                    <option class="form-group" 
                        v-for="document in documentTypes" 
                        :key="document.value" 
                        :value="document.value">
                            {{ document.text }}
                    </option>
                </select>
             </div>
             <div class="error" v-if="!$v.city.required">Необходимо указать документ!</div>
             <div class="form-group">
                 <label class="form__label">Серия</label>
                 <input class="form__input" v-model.trim="$v.serialDoc.$model"/>
             </div>
             <div class="form-group">
                 <label class="form__label">Номер</label>
                 <input class="form__input" v-model.trim="$v.numberDoc.$model"/>
             </div>
             <div class="form-group">
                 <label class="form__label">Кем выдан</label>
                 <input class="form__input" v-model.trim="$v.issuedBy.$model"/>
             </div>
             <div class="form-group" :class="{ 'form-group--error': $v.documentType.$error }">
                 <label class="form__label">Дата выдачи</label>
                 <input class="form__input" v-model.trim="$v.issuedDate.$model"/>
             </div>
             <div class="error" v-if="!$v.issuedDate.required">Дата выдачи должна быть введена в формате дд.мм.гггг</div>
             
        </div>

        <!-- кнопка -->
        <div class="button-wrap">
            <button class="button" type="submit" :disabled="submitStatus === 'PENDING'">Отправить!</button>
            <p class="typo__p" v-if="submitStatus === 'OK'">Клиент успешно создан!</p>
            <p class="typo__p" v-if="submitStatus === 'ERROR'">Не все поля заполнены верно!</p>
            <p class="typo__p" v-if="submitStatus === 'PENDING'">Отправка...</p>
        </div>
    </form>
</template>

<script>
import { required, minLength } from 'vuelidate/lib/validators'
const isPhone = (value) => /^(?!\+.*\(.*\).*\-\-.*$)(?!\+.*\(.*\).*\-$)(?!\+$)(?!\($)(?!\)$)(?!_$)(7[0-9]{10})$/.test(value),
    isBirthday = (value) => /^[0-9]{1,2}\.[0-9]{2}\.[0-9]{4}$/.test(value)
export default {
  data() {
    return {
    //   Основная информация
      name: '',
      surname: '',
      birthday: '',
      patronymic: '',
      phone: '',
      smsCheck: false,
      multiSelect: [],
      submitStatus: null,
      options: [
          {text: 'VIP', value: 'vip'},
          {text: 'Проблемные', value: 'withProblems'},
          {text: 'ОМС', value: 'oms'}
      ],
      gender: '',
      genders: [
          { text: 'Мужской', value: 'male'},
          { text: 'Женский', value: 'female'}
      ],
      doctor: {},
      doctors: [
          { text: 'Иванов', value: 'Ivanov' },
          { text: 'Захаров', value: 'Zaharov' },
          { text: 'Чернышева', value: 'Chernysheva' }
      ],
    //   Адрес
      index: '',
      country: '',
      region: '',
      city: '', //required
      street: '',
      house: '',
    //   passport
      documentType: '',
      documentTypes: [
          { text: 'Пасспорт', value: 'passport' },
          { text: 'Свидетельство о рождении', value: 'birthCertificate' },
          { text: 'Водительское удостоверение', value: 'driversLicense' }
      ],
      serialDoc: '',
      numberDoc: '',
      issuedBy: '',
      issuedDate: '' //required
    }
  },
  validations: {
   // fst info
    name: {
      required,
      minLength: minLength(2)
    },
    surname: {
        required,
        minLength: minLength(2)
    },
    birthday: {
        required,
        isBirthday: isBirthday
    },
    phone: {
        required,
        phoneValid: isPhone
    },
    multiSelect: {
        required
    },
    patronymic: {},
    smsCheck: {},
    gender: {},
    doctor: {},
   // adress info
    index: {},
    country: {},
    region: {},
    city: {
        required
    },
    street: {},
    house: {},
   // passport info
    documentType: {required},
    serialDoc: {},
    numberDoc: {},
    issuedBy: {},
    issuedDate: {
        required,
        isBirthday: isBirthday
    }
  },
  methods: {
    submit() {
      console.log('submit!')
      this.$v.$touch()
      if (this.$v.$invalid) {
        this.submitStatus = 'ERROR'
      } else {
        const formData = {
            name: this.name,
            surname: this.surname,
            birthday: this.birthday,
            patronymic: this.patronymic,
            phone: this.phone,
            smsCheck: this.smsCheck,
            multiSelect: this.multiSelect,
            gender: this.gender,
            doctor: this.doctor,
            index: this.index,
            country: this.country,
            region: this.region,
            city: this.city,
            street: this.street,
            house: this.house,
            documentType: this.documentType,
            serialDoc: this.serialDoc,
            numberDoc: this.numberDoc,
            issuedBy: this.issuedBy,
            issuedDate: this.issuedDate
        }
        console.log(formData)
        this.submitStatus = 'PENDING'
        setTimeout(() => {
          this.submitStatus = 'OK'
        }, 500)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.wrap{
    padding: 1.5rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    height: auto;
    align-items: flex-start;
    .form-group{
        display: flex;
        flex-direction: column;
        margin-bottom: 1rem;
        width: 100%;
        label{
            text-align: left;
            padding-left: 10px;
            margin-bottom: .5rem;
        }
        select{

        }
    }
    .error{
        display: none;
        text-align: left;
        font-size: 10px;
        padding-left: 12px;
        margin-top: -.9rem;
        margin-bottom: 1rem;
        color: red;
    }
    .form-group--error + .error{
        display: block;
    }
    .form-group--error > input,.form-group--error > select{
        border: 1px solid red;
    }
    #options{
        display: flex;
        flex-direction: row;
        padding: .5rem;
        justify-content: space-between;
        width: max-content;
        border: 1px solid gray;
        border-radius: 10px;
        .form-group{
            align-items: center;
            width: min-content;
            margin: .2rem .5rem;    
            input{margin-left: 0;}
            label{padding-left: 0;}
        }
    }
}
.button-wrap{
    width: 100%;
    margin: 0 auto;
    .typo__p{
        margin-bottom: 2rem;
    }
}
</style>>
